.button {
	width: 120px;
	height: 50px;
	font-weight: 500;
	font-size: 16px;
	color: var(--white);
	background: var(--primary-blue);
	border: none;
	border-radius: 8px;
	cursor: pointer;
	text-transform: uppercase;
	transition: all 0.4s;

	&:hover {
		transition: all 0.4s;
		opacity: 0.5;
	}
}
