:root {
	--primary-blue: hsl(238, 40%, 52%);
	--primary-red: hsl(358, 79%, 66%);
	--light-blue: hsl(239, 57%, 85%);
	--light-red: hsl(357, 100%, 86%);
	--dark-blue: hsl(212, 24%, 26%);
	--dark-gray: hsl(211, 10%, 45%);
	--gray: hsl(223, 19%, 93%);
	--light-gray: hsl(228, 33%, 97%);
	--white: hsl(0, 0%, 100%);
}
